import React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import ShareIcon from '@material-ui/icons/Share';
import Divider from '@material-ui/core/Divider';

const useStyles = makeStyles(theme => ({
  shareButton: {
    position: "absolute",
    right: theme.spacing(0),
    bottom: theme.spacing(0),
  },
  shareMenu: {
    // width: "400px",
  },
  iconOnly: {
    height: "40px",
  },
  imageIcon: {
    height: '100%'
  },
  iconRoot: {
    textAlign: 'center'
  }
}));

export default function ShareButton(props) {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);

  function handleClick(event) {
    setAnchorEl(event.currentTarget);
  }

  function handleClose() {
    setAnchorEl(null);
  }

  function popupCenter(url, title, w, h) {
    var userAgent = navigator.userAgent,
      mobile = function() {
        return /\b(iPhone|iP[ao]d)/.test(userAgent) ||
          /\b(iP[ao]d)/.test(userAgent) ||
          /Android/i.test(userAgent) ||
          /Mobile/i.test(userAgent);
      },
      screenX = typeof window.screenX != 'undefined' ? window.screenX : window.screenLeft,
      screenY = typeof window.screenY != 'undefined' ? window.screenY : window.screenTop,
      outerWidth = typeof window.outerWidth != 'undefined' ? window.outerWidth : document.documentElement.clientWidth,
      outerHeight = typeof window.outerHeight != 'undefined' ? window.outerHeight : document.documentElement.clientHeight - 22,
      targetWidth = mobile() ? null : w,
      targetHeight = mobile() ? null : h,
      V = screenX < 0 ? window.screen.width + screenX : screenX,
      left = parseInt(V + (outerWidth - targetWidth) / 2, 10),
      right = parseInt(screenY + (outerHeight - targetHeight) / 2.5, 10),
      features = [];
    if (targetWidth !== null) {
      features.push('width=' + targetWidth);
    }
    if (targetHeight !== null) {
      features.push('height=' + targetHeight);
    }
    features.push('left=' + left);
    features.push('top=' + right);
    features.push('scrollbars=1');

    var newWindow = window.open(url, title, features.join(','));

    if (window.focus) {
      newWindow.focus();
    }

    return newWindow;
  }

  function shareKakao() {
    handleClose();

    var numberPattern = /\d+/g;
    var price = parseInt(props.item.price.match(numberPattern).join([]));

    var linkInfo = {
      objectType: 'commerce',
      content: {
        title: props.item.title,
        imageUrl: props.item.image,
        link: {
          mobileWebUrl: props.item.link,
          webUrl: props.item.link
        }
      },
      commerce: {
        regularPrice: price,
      },
      buttons: [
        {
          title: '구매하기',
          link: {
            mobileWebUrl: props.item.link,
            webUrl: props.item.link
          }
        }
      ]
    };

    if (props.item.orgPrice && props.item.orgPrice.length > 0) {
      linkInfo.commerce.discountPrice = linkInfo.commerce.regularPrice;
      linkInfo.commerce.regularPrice = parseInt(props.item.orgPrice.match(numberPattern).join([]));
    }
    window.Kakao.Link.sendDefault(linkInfo);
  }

  function shareKakaoStory() {
    handleClose();

    window.Kakao.Story.share({
      url: props.item.link,
      text: "[" + props.item.mallName + "]" + props.item.title + " " + props.item.price
    });
  }

  function shareLink(media) {
    handleClose();

    var popupTitle = "링크 공유하기";
    var width = 400;
    var height = 600;
    var itemLink = encodeURIComponent(props.item.link);
    var itemTitle = encodeURI("[" + props.item.mallName + "]" + props.item.title + " " + props.item.price);
    var shareURL = "";

    switch (media) {
      case "naver":
        popupTitle = "네이버 블로그로 공유하기";
        shareURL = "https://share.naver.com/web/shareView.nhn?url=" + itemLink + "&title=" + itemTitle;
        break;
      case "twitter":
        popupTitle = "트위터로 공유하기";
        shareURL = "https://twitter.com/intent/tweet?text=" + itemTitle + "%20" + itemLink;
        height = 264;
        break;
      case "facebook":
        popupTitle = "페이스북으로 공유하기";
        shareURL = "https://www.facebook.com/sharer/sharer.php?u=" + itemLink + "&quote=" + itemTitle;
        width = 600;
        break;
      case "line":
        width = 600;
        height = 550;
        popupTitle = "라인으로 공유하기";
        shareURL = "https://social-plugins.line.me/lineit/share?url=" + itemLink + "&text=" + itemTitle;
        break;
      default:
        break;
    }

    popupCenter(shareURL, popupTitle, width, height);
  }


  return (
    <div>
      <IconButton aria-controls="simple-menu" aria-haspopup="true" className={classes.shareButton} color="inherit" aria-label="Share" onClick={handleClick}>
        <ShareIcon />
      </IconButton>
      <Menu className={classes.shareMenu} id="simple-menu" anchorEl={anchorEl} keepMounted open={Boolean(anchorEl)} onClose={handleClose}>
        <IconButton onClick={() => shareKakao()}>
          <img className={classes.iconOnly} src={process.env.PUBLIC_URL + '/assets/icons/share_kakao.png'} alt="카카오톡 공유하기"/>
        </IconButton>
        <IconButton onClick={() => shareKakaoStory()}>
          <img className={classes.iconOnly} src={process.env.PUBLIC_URL + '/assets/icons/share_kakaostory.png'} alt="카카오스토리 공유하기" />
        </IconButton>
        <IconButton onClick={() => shareLink("naver")}>
          <img className={classes.iconOnly} src={process.env.PUBLIC_URL + '/assets/icons/naver.svg'} alt="네이버 공유하기" />
        </IconButton>
        <Divider light={true} variant="middle" />
        <IconButton onClick={() => shareLink("line")}>
          <img className={classes.iconOnly} src={process.env.PUBLIC_URL + '/assets/icons/share_line.png'} alt="라인 공유하기" />
        </IconButton>
        <IconButton onClick={() => shareLink("facebook")}>
          <img className={classes.iconOnly} src={process.env.PUBLIC_URL + '/assets/icons/facebook.svg'} alt="페이스북 공유하기" />
        </IconButton>
        <IconButton onClick={() => shareLink("twitter")}>
          <img className={classes.iconOnly} src={process.env.PUBLIC_URL + '/assets/icons/share_twitter.png'} alt="트위터 공유하기" />
        </IconButton>
      </Menu>
    </div>
  );
}
