import Consts from '../consts'
import React, {useState, useEffect} from 'react'
import clsx from 'clsx';
import {createStyles, makeStyles, Theme, useTheme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Link from '@material-ui/core/Link';
import NavBarLogin from './NavBarLogin'
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from '@material-ui/icons/Menu';
import Drawer from "@material-ui/core/Drawer";
import Divider from "@material-ui/core/Divider";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import LazyLoad from 'react-lazyload';
import Logo from './Logo'

const drawerWidth = 240;

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      paddingTop: 56,
      display: 'flex',
    },
    menuButton: {
      marginRight: theme.spacing(0),
    },
    hide: {
      display: 'none',
    },
    drawer: {
      width: drawerWidth,
      flexShrink: 0,
    },
    drawerPaper: {
      width: drawerWidth,
    },
    drawerHeader: {
      display: 'flex',
      alignItems: 'center',
      padding: '0 8px',
      ...theme.mixins.toolbar,
      justifyContent: 'flex-end',
    },
    drawerIconBox: {
      flexGrow: 1,
      paddingTop: 10,
      marginLeft: 20,
      width: drawerWidth - 20,
    },
    logo: {
      flexGrow: 1,
    },
    imageIcon: {
      height: '100%'
    },
    iconRoot: {
      textAlign: 'center'
    },
    linkIcon: {
      height: '40px',
    },
    iconInstall: {
      textAlign: 'center',
    },
    iconHide: {
      display: 'none',
    },
  }),
);

export default function NavBar(props) {
  const classes = useStyles();
  const theme = useTheme();

  const [drawerOpen, setDrawerOpen] = useState(false);
  const [showInstallBtn, setShowInstallBtn] = useState(false);

  function handleDrawerOpen() {
    setDrawerOpen(true);
  }

  function handleDrawerClose() {
    setDrawerOpen(false);
  }

  function handleChangeMenu(index) {
    handleDrawerClose();
    if (props.menuIndex !== index) {
      props.onChangeMenu(index);
    }
  }

  function handleInstall() {
    const promptEvent = window.deferredPrompt;
    if (!promptEvent) {
      // The deferred prompt isn't available.
      return;
    }
    // Show the install prompt.
    promptEvent.prompt();
    // Log the result
    promptEvent.userChoice.then((result) => {
      // Reset the deferred prompt variable, since
      // prompt() can only be called once.
      window.deferredPrompt = null;
      // Hide the install button.
      setShowInstallBtn(false);
    });
  }

  useEffect( () => {
    window.addEventListener('beforeinstallprompt', (event) => {
      window.deferredPrompt = event;
      setShowInstallBtn(true);
    });
  }, []);

  return(
    <header className={classes.root}>
      <AppBar position="fixed"
              className={clsx(classes.appBar, {[classes.appBarShift]: drawerOpen})}>
        <Toolbar>
          <IconButton color="inherit" aria-label="Open drawer" onClick={handleDrawerOpen} edge="start"
                      className={classes.menuButton}>
            <MenuIcon />
          </IconButton>
          <Link href={process.env.PUBLIC_URL + '/'} className={classes.logo}>
            {
              process.env.REACT_APP_LOGO_DRAWING
                ?
                  <Logo/>
                :
                  <img src={process.env.PUBLIC_URL + '/logo3.png'} alt="로고" />
            }
          </Link>
          <NavBarLogin cookies={props.cookies}/>
          <IconButton onClick={handleInstall} className={showInstallBtn ? classes.iconInstall : classes.iconHide}>
            <img className={classes.imageIcon} src={process.env.PUBLIC_URL + "/assets/icons/install.svg"} alt="설치" />
          </IconButton>
          <IconButton href={process.env.PUBLIC_URL + '/'} classes={{root:classes.iconRoot}}>
            <img className={classes.imageIcon} src={process.env.PUBLIC_URL + "/assets/icons/refresh.svg"} alt="새로고침"/>
          </IconButton>
        </Toolbar>
      </AppBar>
      <Drawer className={classes.drawer} variant="persistent" anchor="left" open={drawerOpen}
              classes={{paper: classes.drawerPaper,}} onClose={handleDrawerClose}>
        <div className={classes.drawerHeader}>
          <Link onClick={handleDrawerClose} className={classes.logo}>
            <img src={process.env.PUBLIC_URL + '/logo3.png'} alt="로고" />
          </Link>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
          </IconButton>
        </div>
        <Divider />
        <List>
          {
            Consts.SET_OF_MENUS.map((item, index) => (
              <ListItem button key={index} onClick={event => handleChangeMenu(index)}>
                <ListItemText primary={item.displayName} />
              </ListItem>
            ))
          }
        </List>
        <Divider />
        <div className={classes.drawerIconBox}>
          {
            Consts.SET_OF_LINKS.map((item, index) => (
              <LazyLoad key={index}>
                <IconButton target="_blank" rel="noopener noreferrer" href={item.link}>
                    <img className={classes.linkIcon} src={item.image} alt={item.name}/>
                </IconButton>
              </LazyLoad>
          ))
          }
        </div>
      </Drawer>
    </header>
  )
};
