import React from "react";
import {makeStyles} from "@material-ui/core";

const useStyles = makeStyles(theme => ({
  buttonRoot: {
    display: "flex",
  },
  divider: {
    width: "1px",
    background:"#FFFFFF",
  },
  buttonIconArea: {
    width: "35px",
    height: "35px",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  buttonIcon: {
    maxHeight: "100%",
    maxWidth: "100%",
  },
  buttonTextArea: props => ({
    width: "199px",
    height: "35px",
    background: props.data && props.data.backgroundColor ? props.data.backgroundColor : "#FFFFFF",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  }),
  buttonText: props => ({
    fontFamily: "'Nanum Gothic',sans-serif",
    fontWeight: "bold",
    fontSize: "12px",
    color: props.data && props.data.textColor ? props.data.textColor : "#000000",
    maxHeight: "100%",
    maxWidth: "100%",
    marginLeft: "auto",
    marginRight: "auto",
  })
}));

export default function NaverLoginButton(props) {
  const classes = useStyles(props);

  return(
    <div>
      {
        props.data &&
        <div className={classes.buttonRoot} onClick={props.onClick}>
          <div className={classes.buttonIconArea}>
            <img className={classes.buttonIcon} src={props.data.icon}
                 alt={props.data.text}/>
          </div>
          <div className={classes.divider}/>
          <div className={classes.buttonTextArea}><span
            className={classes.buttonText}>{props.data.text}</span></div>
        </div>
      }
    </div>
  )
}
