import React, {useEffect} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import SocialLoginButton from './common/SocialLoginButton'
import Consts from '../consts'

const useStyles = makeStyles(theme => ({
  imageIcon: {
    height: '100%'
  },
  iconRoot: {
    textAlign: 'center'
  },
  iconOnly: {
    height: '40px',
    marginLeft: "auto",
    marginRight: "auto",
  },
}));

export default function NavBarLogin(props) {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [token, setToken] = React.useState({login:false, token:null, id:null, type:null});

  function popupCenter(url, title, w, h) {
    var userAgent = navigator.userAgent,
      mobile = function() {
        return /\b(iPhone|iP[ao]d)/.test(userAgent) ||
          /\b(iP[ao]d)/.test(userAgent) ||
          /Android/i.test(userAgent) ||
          /Mobile/i.test(userAgent);
      },
      screenX = typeof window.screenX != 'undefined' ? window.screenX : window.screenLeft,
      screenY = typeof window.screenY != 'undefined' ? window.screenY : window.screenTop,
      outerWidth = typeof window.outerWidth != 'undefined' ? window.outerWidth : document.documentElement.clientWidth,
      outerHeight = typeof window.outerHeight != 'undefined' ? window.outerHeight : document.documentElement.clientHeight - 22,
      targetWidth = mobile() ? null : w,
      targetHeight = mobile() ? null : h,
      V = screenX < 0 ? window.screen.width + screenX : screenX,
      left = parseInt(V + (outerWidth - targetWidth) / 2, 10),
      right = parseInt(screenY + (outerHeight - targetHeight) / 2.5, 10),
      features = [];
    if (targetWidth !== null) {
      features.push('width=' + targetWidth);
    }
    if (targetHeight !== null) {
      features.push('height=' + targetHeight);
    }
    features.push('left=' + left);
    features.push('top=' + right);
    features.push('scrollbars=1');

    var newWindow = window.open(url, title, features.join(','));

    if (window.focus) {
      newWindow.focus();
    }

    return newWindow;
  }

  function handleClick(event) {
    setAnchorEl(event.currentTarget);
  }

  function handleClose() {
    setAnchorEl(null);
  }

  function logout(media) {
    props.cookies.remove('sc_t', { path: '/' , domain: process.env.REACT_APP_COOKIE_DOMAIN});
    window.location.href = process.env.PUBLIC_URL;
  }

  function login(media) {
    handleClose();

    var popupTitle = "로그인";
    var width = 500;
    var height = 600;
    var loginURL = "";

    switch (media) {
      case "naver":
        popupTitle = "네이버 아이디로 로그인";
        loginURL = "https://nid.naver.com/oauth2.0/authorize?response_type=code&client_id="
          + process.env.REACT_APP_NAVER_CLIENTID + "&redirect_uri=" + Consts.NAVER_LOGIN_CALLBACK_URL;
        break;
      case "kakao":
        popupTitle = "카카오 로그인";
        loginURL = "https://kauth.kakao.com/oauth/authorize?client_id="
          + process.env.REACT_APP_KAKAO_APPKEY + "&redirect_uri=" + Consts.KAKAO_LOGIN_CALLBACK_URL + '&response_type=code';
        break;
      case "facebook":
        popupTitle = "페이스북으로 로그인";
        loginURL = "https://www.facebook.com/v3.3/dialog/oauth?client_id="
          + process.env.REACT_APP_FACEBOOK_APPID + "&redirect_uri=" + Consts.FACEBOOK_LOGIN_CALLBACK_URL;
        break;
      default:
        break;
    }
    console.log(loginURL);

    popupCenter(loginURL, popupTitle, width, height);
  }

  useEffect(() => {
    if (props.cookies && props.cookies.get('sc_t')) {
      var tokenSplit = props.cookies.get('sc_t').split(';');
      if (tokenSplit.length < 3) {
        setToken({login:false, token: null, id: null, type: null});
      } else {
        setToken({
          login: true,
          token: tokenSplit[0],
          id: tokenSplit[1],
          type: tokenSplit[2]
        })
      }
    }
  },[props.cookies, token.login]);

  return (
    <div>
      <IconButton aria-controls="login-menu" aria-haspopup="true" className={classes.iconRoot} color="inherit" aria-label="Login" onClick={handleClick}>
        <img className={classes.imageIcon} src={token.id ? process.env.PUBLIC_URL + "/assets/icons/mypage.svg" : process.env.PUBLIC_URL + "/assets/icons/login.svg"} alt={token.id ? "로그아웃" : "로그인"} />
      </IconButton>
      {
        token.id
          ?
          <Menu className={classes.loginMenu} id="login-menu" anchorEl={anchorEl} keepMounted open={Boolean(anchorEl)} onClose={handleClose}>
            {
              token.type === "naver" &&
              <MenuItem className={classes.loginMenuItem}>
                <SocialLoginButton onClick={() => logout('naver')} data={
                  {
                    icon: process.env.PUBLIC_URL + "/assets/icons/naver.svg",
                    text: "로그아웃",
                    backgroundColor: "#1EC800",
                    textColor: "#FFFFFF",
                  }
                }/>
              </MenuItem>
            }
            {
              token.type === "kakao" &&
              <MenuItem className={classes.loginMenuItem}>
                <SocialLoginButton onClick={() => logout('kakao')} data={
                  {
                    icon: process.env.PUBLIC_URL + "/assets/icons/kakao.png",
                    text: "로그아웃",
                    backgroundColor: "#FFEB00",
                    textColor: "#3C1E1E",
                  }
                }/>
              </MenuItem>
            }
            {
              token.type === "facebook" &&
              <MenuItem className={classes.loginMenuItem}>
                <SocialLoginButton onClick={() => logout('facebook')} data={
                  {
                    icon: process.env.PUBLIC_URL + "/assets/icons/facebook.svg",
                    text: "로그아웃",
                    backgroundColor: "#3b5998",
                    textColor: "#ffffff",
                  }
                }/>
              </MenuItem>
            }
          </Menu>
          :
          <Menu className={classes.loginMenu} id="login-menu" anchorEl={anchorEl} keepMounted open={Boolean(anchorEl)} onClose={handleClose}>
            <MenuItem className={classes.loginMenuItem} onClick={handleClose}>
              <SocialLoginButton onClick={() => login('naver')} data={
                {
                  icon:process.env.PUBLIC_URL + "/assets/icons/naver.svg",
                  text:"네이버 아이디로 로그인",
                  backgroundColor:"#1EC800",
                  textColor:"#FFFFFF",
                }
              } />
            </MenuItem>
            <MenuItem className={classes.loginMenuItem} onClick={handleClose}>
              <SocialLoginButton onClick={() => login('kakao')} data={
                {
                  icon:process.env.PUBLIC_URL + "/assets/icons/kakao.png",
                  text:"카카오 로그인",
                  backgroundColor:"#FFEB00",
                  textColor:"#3C1E1E",
                }
              } />
            </MenuItem>
            <MenuItem className={classes.loginMenuItem} onClick={handleClose}>
              <SocialLoginButton onClick={() => login('facebook')} data={
                {
                  icon:process.env.PUBLIC_URL + "/assets/icons/facebook.svg",
                  text:"Facebook으로 로그인",
                  backgroundColor:"#3b5998",
                  textColor:"#ffffff",
                }
              } />
            </MenuItem>
          </Menu>
      }
    </div>
  );
}
