import React, { Component } from 'react';
import {Switch, Route, BrowserRouter} from 'react-router-dom'
import App from './App';
// import IconViewer from './IconViewer'
import {CookiesProvider, withCookies} from 'react-cookie';
import {MuiThemeProvider} from "@material-ui/core/styles";
import Consts from "./consts";

class TopApp extends Component {
  render() {
    return (
      <MuiThemeProvider theme = { Consts.THEME }>
        <CookiesProvider>
          <BrowserRouter>
            <Switch>
              {/*<Route path="/icon" exact render={props => (<IconViewer {...props} cookies={this.props.cookies}/>)}/>*/}
              <Route path="/:menu" render={props => (<App {...props} cookies={this.props.cookies}/>)}/>
              <Route path="/" render={props => (<App {...props} cookies={this.props.cookies}/>)}/>
            </Switch>
          </BrowserRouter>
        </CookiesProvider>
      </MuiThemeProvider>
    );
  }
}

export default withCookies(TopApp);
