import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import Link from '@material-ui/core/Link';
import Typography from '@material-ui/core/Typography';
import ShareButton from './ShareButton';
import LazyLoad from 'react-lazyload';
import AdSense from "./common/AdSense";

const useStyles = makeStyles(theme => ({
  card: {
    width: (window.innerWidth - 32) / 2 < 220 ? (window.innerWidth - 32) / 2 : 220,
    height: (window.innerWidth - 32) / 2 < 200 ? 380 - (200-(window.innerWidth - 32) / 2): 380,
    position: "relative",
  },
  media: {
    width: "100%",
    height: (window.innerWidth - 32) / 2 < 200 ? (window.innerWidth - 32) / 2 : 200,
    borderRadius: "5px",
  },
  itemTitle: {
    overflow: "hidden",
    height: "80px",
    fontSize: "14px",
    marginTop: "5px",
    marginLeft: "10px",
    marginRight: "10px",
  },
  itemPriceArea: {
    position: "absolute",
    left: theme.spacing(2),
    bottom: theme.spacing(1),
  },
  itemPrice: {
    float: "left",
    clear: "both",
    fontWeight: 700,
    fontSize: "20px",
    marginTop: "2px",
    lineHeight: "1",
    letterSpacing: "-.06em",
    fontStyle: "normal",
    textAlign: "center",
  },
  itemDiscount: {
    float:"left",
    fontSize:"14px",
    color:"#C5500D",
    letterSpacing:"0",
    verticalAlign:"top",
    fontWeight: 700,
  },
  itemOrgPrice: {
    float:"left",
    marginTop:"1px",
    marginRight:"4px",
    marginLeft:"5px",
    fontWeight: 700,
    fontSize:"14px",
    color:"#6E7481",
    textDecoration: "line-through",
    fontStyle: "normal",
  },
  sticker: {
    width: "60px",
    height: "30px",
    borderRadius: "10px",
    marginTop: "2px",
    left: theme.spacing(1),
  }
}));

function getMallSticker(mallName) {
  var imgUrl = "";
  switch (mallName) {
    case "티몬":
      imgUrl = "https://img.linkprice.com/files/glink/tmon/20170627/5951a3c576be9_120_60.jpg";
      break;
    case "G마켓":
      imgUrl = "https://img.linkprice.com/files/stlink/gmarket/0364";
      break;
    case "11번가":
      imgUrl = "https://img.linkprice.com/files/glink/11st/20151209/56677fb2594fc_120_60.jpg";
      break;
    case "쿠팡":
      imgUrl = "https://img.linkprice.com/files/glink/coupang/20180115/5a5c6d9d5b3f8_120_60.jpg";
      break;
    case "위메프":
      imgUrl = "https://image.wemakeprice.com/dealimg2/today_120x60(1).jpg";
      break;
    case "GSSHOP":
      imgUrl = "https://img.linkprice.com/files/glink/gseshop/12060.jpg";
      break;
    default:
      break;
  }
  return imgUrl;
}

export default function ProductItem(props) {
  const classes = useStyles();

  return (
      <Card className={classes.card} justify="center">
        {
          props.item ?
          <Link target="_blank" rel="noopener noreferrer" href={props.item.link} color="inherit" className={classes.link}>
            <LazyLoad>
              <div style={{display: 'flex', justifyContent: 'center'}}>
                  <CardMedia justify="center" className={classes.media} image={props.item.image}
                             title={props.item.title}/>
              </div>
              <CardMedia className={classes.sticker} image={getMallSticker(props.item.mallName)}
                         title={props.item.mallName}/>
            </LazyLoad>
            <Typography className={classes.itemTitle}>{props.item.title}</Typography>
            <div className={classes.itemPriceArea}>
              {
                (props.item.discount || props.item.orgPrice) ?
                  <Typography className={classes.itemDiscount}>{props.item.discount}</Typography>
                  :
                  <Typography className={classes.itemDiscount}>&nbsp;</Typography>
              }
              <Typography className={classes.itemOrgPrice}>{props.item.orgPrice}</Typography>
              <Typography className={classes.itemPrice}>{props.item.price}</Typography>
            </div>
          </Link>
          :
          <AdSense adClient="ca-pub-2682155550928874" adSlot="4097750891" adFormat="auto" fullWidthResponsive />
        }
        {
          props.item &&
          <ShareButton item={props.item}/>
        }
      </Card>
  );
}
