import React from 'react'
import NavBar from './components/NavBar'
import ProductList from './components/ProductList'
import Consts from "./consts";
import AdSense from './components/common/AdSense';

export default function App(props) {
  const [menuIndex, setMenuIndex] = React.useState(getMenuIndex(props.match && props.match.params && props.match.params.menu));

  function getMenuIndex(queryName) {
    for (var i = 0; i < Consts.SET_OF_MENUS.length; i++) {
      if (Consts.SET_OF_MENUS[i].queryName === queryName) {
        return i;
      }
    }
    return 0;
  }

  function onChangeMenu(menuIndex) {
    setMenuIndex(menuIndex);
  }

  return (
    <div>
      <NavBar menuIndex={menuIndex} onChangeMenu={onChangeMenu} cookies={props.cookies} />
      <AdSense customStyle={{marginTop:"15px"}} adClient='ca-pub-2682155550928874' adSlot='8984188016' adFormat='auto' fullWidthResponsive />
      <ProductList menuIndex={menuIndex} onChangeMenu={onChangeMenu}/>
    </div>
  )
}
